<template>
  <div class="container">
    <h1>Notas</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar Nota</h3>
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
        v-model="notaEditar.nombre"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
        v-model="notaEditar.descripcion"
      />
      <b-button class="btn-success my-2 mx-2" type="submit">Editar</b-button>
      <b-button class="my-2" @click="editar=false">Cancelar</b-button>
    </form>

    <form @submit.prevent="agregarNota()" v-if="!editar">
      <h3>Agregar nueva nota</h3>

      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
        v-model="nota.nombre"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
        v-model="nota.descripcion"
      />
      <b-button type="submit" class="btn-success my-2">Agregar</b-button>
    </form>
    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nota</th>
          <th scope="col">Descripción</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{ item._id }}</th>
          <td>{{ item.nombre }}</td>
          <td>{{ item.descripcion }}</td>
          <td>
            <b-button
              type="submit"
              @click="eliminarNota(item._id)"
              class="btn-danger mx-2"
              >Eliminar</b-button
            >
            <b-button
              type="submit"
              @click="activarEdicion(item._id)"
              class="btn-warning mx-2"
              >Editar</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notas: [],
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      nota: { nombre: "", descripción: "" },
      editar: false,
      notaEditar: {},
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    listarNotas() {
      this.axios
        .get("/nota")
        // .get("https://ciclo3-notas-new.herokuapp.com/api/nota")
        .then((res) => {
          this.notas = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    agregarNota(item) {
      this.axios
        // .post("nueva-nota", this.nota)
        .post("https://ciclo3-notas-new.herokuapp.com/api/nueva-nota", this.nota)
        .then((res) => {
          // Agrega al inicio de nuestro array notas
          this.notas.push(res.data);
          this.nota.nombre = "";
          this.nota.descripcion = "";
          // Alerta de mensaje
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota Agregada!";
          this.showAlert();
        })
        .catch((e) => {
          //   console.log(e.response);
          // Alerta de mensaje
          this.mensaje.color = "danger";
          this.mensaje.texto = "Ingrese los datos!";
          this.showAlert();
        });
      //   this.notas = {};
    },

    eliminarNota(id) {
      this.axios
        // .delete(`/nota/${id}`)
        .delete(`https://ciclo3-notas-new.herokuapp.com/api/nota/${id}`)
        .then((res) => {
          const index = this.notas.findIndex(
            (item) => item._id === res.data._id
          );
          this.notas.splice(index, 1);
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota Eliminada!";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    activarEdicion(id) {
      this.editar = true;
      this.axios
        // .get(`/nota/${id}`)
        .get(`https://ciclo3-notas-new.herokuapp.com/api/nota/${id}`)
        .then((res) => {
          this.notaEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    editarNota(item) {
      this.axios
        // .put(`/nota/${item._id}`, item)
        .put(`https://ciclo3-notas-new.herokuapp.com/api/nota/${item._id}`, item)
        .then((res) => {
          const index = this.notas.findIndex((n) => n._id === res.data._id);
          this.notas[index].nombre = res.data.nombre;
          this.notas[index].descripcion = res.data.descripcion;
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota editada!";
          this.showAlert();
          this.editar = false;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
};
</script>
